import React from 'react';
import $ from 'jquery';
import {Swiper, SwiperSlide} from 'swiper/react';
import {Pagination, Autoplay} from 'swiper/modules';
import 'swiper/css';
import 'swiper/css/pagination';

function Slide(props) {
  return (
    <>
      <article className='slide'>
        <Swiper 
          modules={[Pagination, Autoplay]}
          spaceBetween={0}
          slidesPerView={1}
          autoplay={{delay:3000}}
          pagination={{clickable:true}}>
            <SwiperSlide>
              <img src={`${process.env.PUBLIC_URL}/images/slide1.jpg`} alt="" />
            </SwiperSlide>
            <SwiperSlide>
              <img src={`${process.env.PUBLIC_URL}/images/slide2.jpg`} alt="" />
            </SwiperSlide>
            <SwiperSlide>
              <img src={`${process.env.PUBLIC_URL}/images/slide3.jpg`} alt="" />
            </SwiperSlide>
            <SwiperSlide>
              <img src={`${process.env.PUBLIC_URL}/images/slide4.jpg`} alt="" />
            </SwiperSlide>
        </Swiper>
          
      </article>
    </>
  );
}

export default Slide;